import React, { Component } from 'react';
import { arc, chord, descending, rgb, ribbon, scaleOrdinal, select } from 'd3';
import { loadVisionMap, IVisionMap } from './helpers';
import './App.css';

// https://github.com/d3/d3-chord

/*
  matrix: [
    [ 0, 1, 1 ], // A depends on B and C
    [ 0, 0, 1 ], // B depends on C
    [ 0, 0, 0 ]  // C depends on nothing
  ]
*/
function createMatrix(visionMap: IVisionMap) {
  const keys = Object.keys(visionMap);
  const matrix = [ ...new Array(keys.length) ].map(() => [ ...new Array(keys.length) ]);
  keys.forEach((keyA, i) => {
    const a = matrix[i] = [];
    keys.forEach((keyB, j) => {
      const dep = visionMap[keyA].dependants;
      a[j] = dep.indexOf(keyB) === -1 ? 0 : 1; //dep.length
    });
  });
  return matrix
}

class Visualization extends Component {

constructor() {
  super();
    this.state = {
      vision: []
    };
  }

  componentDidMount(): void {
    loadVisionMap().then(visionMap => this.createVisualization(
      Object.keys(visionMap),
      createMatrix(visionMap),
      visionMap
    ));
  }

  createVisualization(labels: string[], matrix, visionMap: IVisionMap): void {
    const MARGIN = 50;
    const PADDING = 50;
    const elem = select('svg');
    const width = +elem.attr('width');
    const height = +elem.attr('height');
    const outerRadius = Math.min(width, height) * 0.5 - MARGIN;
    const innerRadius = outerRadius - PADDING;

    const _chord = chord().padAngle(.1)
                          .sortSubgroups(descending)
                          .sortChords(descending);

    const _arc = arc().innerRadius(innerRadius)
                      .outerRadius(outerRadius);

    const _ribbon = ribbon().radius(innerRadius - 0);

    const color = scaleOrdinal().range([
      '#3ccfcf',
      '#7cff95',
      '#fdf286',
      '#ffcd1f',
      '#f55241',
      '#0f82f7',
      '#ffe582',
      '#04c1fb',
      '#fef063',
      '#ff26ce',
      '#eb0a6a',
      '#1eeccb',
      '#1db8ab',
      '#ffd400',
      '#a45bcd',
      '#e2544d',

      '#B6E0D8',
      '#E4F4FE',
      '#FC7927',
      '#292628',
      '#0C7883',
      '#FF4A43',
      '#00e6b0',
      '#AFD6CC',
      '#7CFF95',
      '#FDF286',
      '#FF3C98',
      '#ff3a1e'
    ]);

    const g = elem.append('g')
                  .attr('transform', `translate(
                    ${ outerRadius + MARGIN },
                    ${ outerRadius + MARGIN })
                  `)
                  .datum(_chord(matrix));

    const group = (
      g.selectAll('_this_does_not_seem_to_matter_')
       .data(({ groups }) => groups)
       .enter()
       .append('g')
       .attr('class', 'group')
    );

    // arcs
    group
      .append('path')
      .style('fill', ({ index }) => color(index))
      .attr('d', _arc)
      .style('stroke', ({ index }) => rgb(color(index)).darker(0.2))
      .on('mouseover', fade(g, 0.1))
      .on('mouseout', fade(g, 1))
      .on('click', (chord, i) => window.location.href = '#' + labels[i]);

    // labels
    group
      .append('text')
      .each(d => d.angle = (d.startAngle + d.endAngle) / 2)
      .attr('dy', '.35em')
      .attr('transform', ({ angle }) => `
        rotate(${ angle * 180 / Math.PI - 90 })
        translate(${ outerRadius + 10 })
        ${ angle > Math.PI ? 'rotate(180)' : '' }
      `)
      .style('text-anchor', ({ angle }) => angle > Math.PI ? 'end' : null)
      .text(({ index }) => labels[index])
      .on('mouseover', fade(g, 0.1))
      .on('mouseout', fade(g, 1))
      .on('click', (chord, i) => window.location.href = '#' + labels[i]);

    // chords
    g.append('g')
     .selectAll()
     .data(chords => chords)
     .enter()
     .append('path')
     .attr('class', 'chord')
     .attr('d', _ribbon)
     .style('fill', ({ target }) => color(target.index));
  }

  render() {
    return <svg width="800" height="800"></svg>;
  }

}

export default Visualization;

function fade(g, opacity: number): void {
  return (chord, i) => {
    g.selectAll('.chord')
      // .style('opacity', 1)
      .filter(({ source, target }) => source.index !== i && target.index !== i)
      .transition()
      .style('opacity', opacity);

    const groups = [i];

    g.selectAll('.chord').filter(({ source, target }) => {
      if (source.index === i) {
        groups.push(target.index);
      }
      if (target.index === i) {
        groups.push(source.index);
      }
    });

    g.selectAll('.group')
     .filter(({ index }) => groups.indexOf(index) === -1)
     .transition()
     .style('opacity', opacity);
  };
}



