import React, { Component } from 'react';
import { loadVisionMap } from './helpers';
import './App.css';

function renderList(list: string[]): HTMLLIElement[] {
  let keyCounter = 0;
  const keyPrefix = Math.random().toString(16).substr(3);
  return list.map(item => <li key={keyPrefix + keyCounter++}>{item}</li>);
}

function renderDependencies(keys: string[], vision): HTMLLIElement[] {
  return keys.map(key => (
    <li key={key}>
      <a href={'#' + key}>
        <span className="bold"><i># </i>{key}</span>
        &nbsp;
        <span>{vision[key].see}</span>
      </a>
    </li>
  ));
}

class App extends Component {

  constructor() {
    super();
    this.state = {
      vision: []
    };
  }

  componentDidMount(): void {
    loadVisionMap().then(vision => this.setState({ vision }));
  }

  render() {
    const { vision } = this.state;
    const keys = Object.keys(vision);
    return (
      <ul className="items">
        {keys.map(key => {
          const { see, todo, not, dependants, dependencies } = vision[key];
          return (
            <li name={key} key={key} className="row">
              <a href="#">▲</a>
              <a name={key} className="key">{key}</a>
              <div className="info">
                <div className="see bold">We see { see && see.toLowerCase() } ...</div>

                { not && <div className="not">and not { not.toLowerCase() }</div> }

                { dependencies.length > 0 &&
                  <div className="row">
                    <span className="bold">Requires:</span>
                    <ul className="deps">{ renderDependencies(dependencies, vision) }</ul>
                  </div>
                }

                { dependants.length > 0 &&
                  <div className="row">
                    <span className="bold">Accomplishes:</span>
                    <ul className="deps">{ renderDependencies(dependants, vision) }</ul>
                  </div>
                }

                { todo.length > 0 &&
                  <div className="row">
                    <span className="bold">Actions:</span>
                    <ol className="deps">{ renderList(todo) }</ol>
                  </div>
                }

              </div>
            </li>
          );
        })}
      </ul>
    );
  }
}

export default App;



