import { json } from 'd3';

export interface IVisionComponent {
  key: string;
  see: string;
  not: string;
  todo: string[];
  dependants: string[];
  dependencies: string[];
}

export interface IVisionMap {
  [key: string]: IVisionComponent;
}

function expandDependencies(chain: string[], key: string, vision: IVisionMap): void {
  return vision[key].dependencies.reduce((accumulator, dependency) => {
    if (chain.indexOf(dependency) > -1) {
      throw new Error(`Circular reference "${dependency}" detected in chain: ${chain.join(', ')}`);
    }
    return expandDependencies(chain.concat(dependency), dependency, vision);
  }, chain);
}

function coerceDependencies(dependencies: string | number): string[] {
  const coerced = [dependencies].join('');
  return coerced.split(' ').filter(isDefined);
}

function dependantsOf(key: string, dependants: { [key:string]: string[]; }): number {
  return Object.keys(dependants).reduce((accumulator, dependant) => (
    dependants[dependant].indexOf(key) === -1
      ? accumulator
      : accumulator.concat(dependant)
  ), []);
}

function isDefined(value: any): boolean {
  return !!value;
}

export function loadVisionMap(): Promise<IVisionMap> {
  return json('./data.json').then(result => {
    const keys = Object.keys(result);
    const vision: IVisionMap = keys.reduce((accumulator, key) => ({
      ...accumulator,
      [key]: {
        todo: [],
        ...result[key],
        key,
        dependencies: coerceDependencies(result[key].dependencies)
      }
    }), {});
    const expandedDependencies = keys.reduce((accumulator, key) => ({
      ...accumulator,
      [key]: expandDependencies([], key, vision)
    }), {});
    return keys.reduce((accumulator, key) => ({ ...accumulator, [key]: {
      ...vision[key],
      dependants: dependantsOf(key, expandedDependencies)
    }}), {});
  });
}



